<template>
  <div class="grid-img-box">

    <viewer :images="data">
      <div
        class="grid-img-item"
        v-for="(i,index) in data"
        :key="index"
      >
        <img
          :src="getImgUrl(i)"
          alt
        />

        <!-- <span
          v-text="i.title"
          class="title"
        ></span>
        <span v-text="i.user"></span> -->
      </div>

    </viewer>
    <Spin
      fix
      v-if="isShow"
    >
      <Icon
        type="load-c"
        size=18
        class="demo-spin-icon-load"
      ></Icon>
      <div>Loading</div>
    </Spin>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isShow: false,
      data: [
        "antiquity0001",
        "antiquity0002",
        "antiquity0003",
        "antiquity0004",
        "antiquity0005",
        "antiquity0006",
        "antiquity0007",
        "antiquity0008",
        "antiquity0009",
        "antiquity0010"
      ]
    };
  },
  created() {
    this.isShow = true;
  },
  mounted() {
    setTimeout(() => {
      this.isShow = false;
      this.init();
    }, 200);
  },
  methods: {
    getImgUrl(src) {
      // return require("@/assets/file/photo_details/scenery/" + src + ".jpg");
      // return require("@/assets/file/photo_details/love/"+src+".jpg");
      return require("@/assets/file/photo_details/antiquity/" + src + ".jpg");
    },
    init() {
      $(".grid-img-box").masonry({
        columnWidth: 20,
        itemSelector: ".grid-img-item", // 要布局的网格元素
        gutter: 20, // 网格间水平方向边距，垂直方向边距使用css的margin-bottom设置
        percentPosition: true, // 使用columnWidth对应元素的百分比尺寸
        stamp: ".grid-stamp", // 网格中的固定元素，不会因重新布局改变位置，移动元素填充到固定元素下方
        fitWidth: true, // 设置网格容器宽度等于网格宽度，这样配合css的auto margin实现居中显示
        originLeft: true, // 默认true网格左对齐，设为false变为右对齐
        originTop: true, // 默认true网格对齐顶部，设为false对齐底部
        containerStyle: { position: "relative" }, // 设置容器样式
        transitionDuration: "0.8s", // 改变位置或变为显示后，重布局变换的持续时间，时间格式为css的时间格式
        stagger: "0.03s", // 重布局时网格并不是一起变换的，排在后面的网格比前一个延迟开始，该项设置延迟时间
        resize: true, // 改变窗口大小将不会影响布局
        initLayout: true
      });
    }
  }
};
</script>
<style >
.demo-spin-icon-load {
  animation: ani-demo-spin 1s linear infinite;
}
@keyframes ani-demo-spin {
  from {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(180deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.demo-spin-col {
  height: 100px;
  position: relative;
  border: 1px solid #eee;
}
</style>
<style lang="scss" src="@/assets/scss/photo_details.scss" scoped>
</style>


